* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  background-color: rgb(23, 20, 24);
  color: #fff;
}
.coin-app {
  display: grid;
  align-items: center;
  margin-top: 1rem;
  color: #fff;

  .coin-search {
    display: grid;
    place-items: center;

    .coin-text {
      margin: auto;
      margin-bottom: 2.5vh;
      text-align: center;
      font-size: calc(18px + 1vh);
    }
    .coin-input {
      padding-left: 1rem;
      min-width: 300px;
      margin: auto;
      margin-bottom: 1.5vh;
      min-height: 50px;
      border-radius: 8px;
      border: none;
      background: linear-gradient(to bottom, #99089e 5%, #85048a 100%);
      box-shadow: inset 0px 10px 14px -7px #64046d;
      color: #e2e2e2;

      &:focus {
        outline: none;
      }

      &::placeholder {
        color: #e2e2e2;
      }
    }
  }
  .values-text {
    margin: 1.5vh 0;
    text-align: center;
    font-size: calc(14px + 0.8vh);
    text-transform: uppercase;
  }
  .btnTgl {
    margin: auto;
    min-width: 300px;
    box-shadow: inset 0px 10px 14px -7px #64046d;
    background: linear-gradient(to bottom, #99089e 5%, #85048a 100%);
    border-radius: 8px;
    cursor: pointer;
    color: #ffffff;
    font-size: calc(18px + 1vh);
    font-weight: bold;
    padding: 13px 32px;
    text-decoration: none;
    text-shadow: 0px 1px 0px #5b326b;
    margin-top: 1rem;
    margin-bottom: 1.5rem;
    &:focus {
      outline: none;
    }
    &:hover {
      background: linear-gradient(to bottom, #8b3d9b 5%, #8b4f97 100%);
      background-color: #408c99;
    }
  }
  .coin-nav {
    display: grid;
    align-items: center;
    width: 85vw;
    height: 5vh;
    margin: auto;
    margin-top: 1.5vh;
    grid-template-columns: 33% 1fr 1fr 1fr 25%;
    text-align: start;
    font-size: 3vh;

    .nav-name {
      padding-left: 2rem;
      text-align: start;
    }
    .nav-percentage {
      margin: auto;
    }
    .nav-price {
      margin-left: 10px;
      // text-align: center;
    }
    .nav-mktcap {
      padding-right: 10px;
      text-align: end;
    }
  }

  @media (max-width: 850px) {
    .coin-nav {
      grid-template-columns: 50% 30% 20%;
      margin: auto;
      font-size: 2.5vh;
    }

    .nav-symbol {
      display: none;
    }
    .nav-mktcap {
      display: none;
    }
  }
}
