.coin-container {
  display: grid;
  justify-content: center;

  .coin-row {
    display: grid;
    align-items: center;
    width: 85vw;
    height: 8vh;
    margin-top: 1.5vh;
    grid-template-columns: 40% 60%;
    align-items: center;
    border-bottom: 1px solid #d7d7d7;

    .coin {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      margin-left: 0;

      & h1 {
        font-size: 2.2vh;
        margin-left: 20px;
      }
      & img {
        margin-left: 0;
        height: 4vh;
      }
      .coin-symbol {
        margin-left: auto;
        padding-right: 20px;
        font-size: 2vh;
        text-transform: uppercase;
      }
    }
    .coin-data {
      display: flex;

      .coin-price {
        margin-right: 30px;
        margin: auto;
      }

      .coin-percent {
        margin: auto;
        margin-left: 20px;
      }
      .red {
        color: #f00606;
      }

      .green {
        color: #11d811;
      }
      .coin-marketcap {
        margin-left: 20px;
      }
    }
  }
}
@media (max-width: 850px) {
  .coin-symbol {
    display: none;
  }

  .coin-container {
    display: grid;
    justify-content: center;
  }
  .coin-row {
    display: grid;
    grid-template-columns: 50% 50%;
    align-items: center;
  }

  .coin {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  & img {
    margin-right: 5px;
  }

  & h1 {
    font-size: calc(10px);
    margin: auto;
  }

  .coin-data {
    display: grid;
    grid-template-columns: 2fr 1fr none;
    overflow: hidden;
  }

  .coin-marketcap {
    display: none;
  }
}
